body {
  min-width: 320px;
  font-family: 'DINPro';
  font-size: 18px;
  line-height: 24px;
  font-weight: 500;
  margin: 0;
  padding: 0;
}

.container {
  width: 320px;
  margin: 0 auto;

}
.container--mod{
  width: 280px;
  margin: 0 auto;
  padding: 0 20px;

}

.visually-hidden {
  position: absolute;
  height: 1px;
  width: 1px;
  overflow: hidden;
  clip: rect(1px, 1px, 1px, 1px);
  clip-path: polygon(0 0, 0 0, 0 0, 0 0);
}

.global__title-hedline {
  font-family: 'DINProCond';
  margin: 0;
  padding: 0;
  font-size: 34px;
  line-height: 37px;
  font-weight: 900;
  text-transform: uppercase;
  color: #004153;
}
.global__text {
  font-family: 'DINPro';
  font-size: 18px;
  line-height: 24px;
  font-weight: 500;
  margin: 0;
  padding: 0;
}









